.rotate-loader {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border-top: 4px solid #3b44e2;
  border-right: 4px solid transparent;
  animation: rotate 1.2s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
